import { TitleCasePipe } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { CommonService } from 'src/app/core/services/common.service';
import { PartyService } from 'src/app/core/services/party.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';
import { WarehouseService } from 'src/app/core/services/warehouse.service';

@Component({
  selector: 'app-add-shipping',
  templateUrl: './add-shipping.component.html',
  styleUrls: ['./add-shipping.component.scss']
})
export class AddShippingComponent implements OnInit {

  public currentUser = JSON.parse(localStorage.getItem("currentUser"))
  public TransferOrderForm: any;
  public submitted: boolean = false;
  public today: Date;
  countryList: any[];

  constructor(private formBuilder: FormBuilder,
    public router: Router, private commonService: CommonService,
    private toastService: ToastNotificationService, public dialog: MatDialog,
    private titleCasePipe: TitleCasePipe,
    // @Inject(MAT_DIALOG_DATA) public data: any,
    public dialogRef: MatDialogRef<AddShippingComponent>,
    private warehouseService: WarehouseService,
    private partyService: PartyService
  ) {
    this.today = new Date();
  }

  ngOnInit(): void {
    try {
      this.TransferOrderForm = this.formBuilder.group({
          name: [null, Validators.required],
          saleInvoice: [null, Validators.required],
      });
      // this.getAllCountries()
      
    } catch (error) {
        this.toastService.toastMsg({
          title: 'Error',
          content: error
        })
    }
  }  
  
  get f() {
    return this.TransferOrderForm.controls;
  }

  cancel() {
    this.dialogRef.close();
  }

  numberOnly(event): boolean {
    const charCode = (event.which) ? event.which : event.keyCode;
    if (charCode > 31 && (charCode < 48 || charCode > 57)) {
      return false;
    }
    return true;
  }

  numberDecimalOnly(event): boolean {
    var charCode = (event.which) ? event.which : event.keyCode;
    if (charCode != 46 && charCode > 31
      && (charCode < 48 || charCode > 57)) {
      return false;
    }
    return true;
  }

  onSave() {
    this.submitted = true;
    console.log(this.TransferOrderForm.value.country)
    if (this.TransferOrderForm.invalid) {
      this.toastService.toastMsg({
        title: "Error",
        content: "Fill All Required Fields.",
      });
      return false;
    } else {
      const data = {
          // "id": "4", //optional
          "userId": this.currentUser.id,
          "name": this.TransferOrderForm.value.name,
          "saleInvoice": this.TransferOrderForm.value.saleInvoice,
      }
      this.warehouseService.postWarehouse(data).then( res => {
        if(res.success) {
          this.dialogRef.close();
          this.commonService.notifyDataAdded()
          this.toastService.toastMsg({
            title: "Success",
            content: "Shipping Data Addedd Successfully!!!",
          });
        }
      })
    }
  }

  // getAllCountries() {
  //   this.partyService.getAllCountries({}).then((res) => {
  //     this.countryList = res.data;
  //   });
  // }
}
