import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { ShippingRoutingModule } from './shipping-routing.module';
import { ShippingComponent } from './shipping.component';
import { ShippingListComponent } from './components/shipping-list/shipping-list.component';
import { AddShippingComponent } from './components/add-shipping/add-shipping.component';
import { RouterModule } from '@angular/router';
import { MaterialExampleModule } from 'src/app/material.module';
import { MatInputModule } from '@angular/material/input';
import { MatButtonModule } from '@angular/material/button';
import { MatIconModule } from '@angular/material/icon';
import { MatNativeDateModule } from '@angular/material/core';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { MatMomentDateModule } from '@angular/material-moment-adapter';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { BsDatepickerModule } from 'ngx-bootstrap/datepicker';
import { EditShippingComponent } from './components/edit-shipping/edit-shipping.component';

@NgModule({
  declarations: [
    ShippingComponent,
    ShippingListComponent,
    AddShippingComponent,
    EditShippingComponent
  ],
  imports: [
    CommonModule,
    ShippingRoutingModule,
    RouterModule,
    MaterialExampleModule,
    MatInputModule,
    MatButtonModule,
    MatIconModule,
    MatNativeDateModule,
    MatFormFieldModule,
    MatDatepickerModule,
    MatMomentDateModule,    
    FormsModule,
    ReactiveFormsModule,
    BsDatepickerModule.forRoot(),
  ]
})
export class ShippingModule { }
